import ImageGallery from 'react-image-gallery';
import { PureComponent } from 'react';
import "./Slider.scss";
import licensia1 from '../../assets/images/slider/licensia1.png'
import licensia2 from '../../assets/images/slider/licensia2.png'
import licensia3 from '../../assets/images/slider/licensia3.png'

const images = [
  {
    original: `${licensia1}`,
  },
  {
    original: `${licensia2}`,
  },
  {
    original: `${licensia3}`,
  },
];

class SimpleSlider extends PureComponent {
  render() {
    return <ImageGallery items={images} />;
  }
}
export default SimpleSlider;
