import { PureComponent } from 'react';
import './Popup.scss';
import { closePopup } from '../../actionCreators';
import { connect } from 'react-redux';

class Popup extends PureComponent {

  handleClick = () => {
    const { closePopup } = this.props;
    closePopup();
  }

  render() {
    const { isPopupOpen } = this.props;
    if (!isPopupOpen) {
      return null;
    }

    return (
      <div>
        <div className='close' onClick={this.handleClick}/>
      <div className="popup" onClick={this.handleClick}>
        <div className="popup__content">
        <iframe src="https://yandex.ru/map-widget/v1/?um=constructor%3A94efec13e0fb1f43ea07f1cf2f9a6123a2c32945de6603c4cfee336017a1de7a&amp;source=constructor" width="100%" height="100%" frameborder="0"></iframe>
        </div>
      </div>
      </div>
    );
  }
}

const mapStateToProps = ({ popup }) => ({
  isPopupOpen: popup.isPopupOpen,
});

const mapDispatchToProps = dispatch => ({
  closePopup: () => dispatch(closePopup())
});

export default connect(mapStateToProps, mapDispatchToProps)(Popup);