import { PureComponent } from 'react';
import './Page.scss'

class Page extends PureComponent {

  render() {
    return (
      <div className='page'>
        <span className='page__title'>Монтаж дымоходов из нержавеющей стали <span className='page__titles'>для частных домов и предприятий</span></span>
        <div className='page__flex-space'>
          <div className='page__flex'>
            <h2>15</h2>
            <div className='page__text'>
              <h4>лет устанавливаем дымоходы </h4>
              <h5>для частных лиц и организаций</h5>
            </div>
          </div>
          <div className='page__flex'>
            <div className='page__text'>
              <h4>Бесплатный выезд инженера </h4>
              <h5>в пределах 30 км. от Киева</h5>
            </div>
          </div>
          <div className='page__flex'>
            <div className='page__text'>
              <h4>Монтаж под ключ от 1 дня, </h4>
              <h5>в наличии все разрешительные документы</h5>
            </div>
          </div>
        </div>
        <div></div>
        <div></div>
      </div>
    );
  }
}

export default Page;