const initState = {
    isPopupOpenSlider: false,
  }
  
  function reducer(state = initState, action) {
    switch (action.type) {
      case "OPEN_POPUP_SLIDER": {
        return { ...state, isPopupOpenSlider: true }
      }
      case "CLOSE_POPUP_SLIDER": {
        return { ...state, isPopupOpenSlider: false }
      }
      default: return state;
    }
  }
  
  export default reducer;
  
  