export function openPopup() {
    return {
      type: 'OPEN_POPUP',
    };
  }
  
  export function closePopup() {
    return {
      type: 'CLOSE_POPUP',
    };
  }
  export function openPopupSlider() {
    return {
      type: 'OPEN_POPUP_SLIDER',
    };
  }
  
  export function closePopupSlider() {
    return {
      type: 'CLOSE_POPUP_SLIDER',
    };
  }
  
  export function openToggle() {   
    return {
      type: 'OPEN_TOGGLE',
    };
  }
  
  export function closeToggle() {
    return {
      type: 'CLOSE_TOGGLE',
    };
  }