import { PureComponent } from 'react';
import './toggle.scss';
import { closeToggle } from '../../actionCreators';
import { connect } from 'react-redux';

class Toggle extends PureComponent {

  handleClick = () => {
    const { closeToggle } = this.props;
    closeToggle();
  }

  render() {
    const { isToggleOpen } = this.props;
    if (!isToggleOpen) {
      return null;
    }

    return (
      <div>
        <div className='close' onClick={this.handleClick} />
        <div className="toggle" onClick={this.handleClick}>
          <div className="toggle__content">
            <div className='toggle__PageMenu'>
              <a href='#Calculater' >КАЛЬКУЛЯТОР</a>
              <a href='#' >УСЛУГИ</a>
              <a href='#' >НАШИ НОВОСТИ</a>
              <a href='#' >ОТЗЫВЫ</a>
              <a href='#' >КОНСУЛЬТАЦИЯ</a>
              <a href='#' >ПРЕИМУЩЕСТВА</a>
              <a href='#' >ПРОИЗВОДСТВО</a>
              <a href='#' >ВОПРОС-ОТВЕТ</a>
              <a href='#' >КОНТАКТЫ</a>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

const mapStateToProps = ({ toggle }) => ({
  isToggleOpen: toggle.isToggleOpen,
});

const mapDispatchToProps = dispatch => ({
  closeToggle: () => dispatch(closeToggle())
});

export default connect(mapStateToProps, mapDispatchToProps)(Toggle);