import { PureComponent } from 'react';
import { openPopup } from '../../actionCreators';
import { openToggle } from '../../actionCreators';
import { connect } from 'react-redux';
import logo from '../../assets/images/header/logo.png';
import call from '../../assets/images/header/call.png';
import viber from '../../assets/images/header/viber.png'
import whatsapp from '../../assets/images/header/whatsapp.png'
import telegram from '../../assets/images/header/telegram.png'
import map from '../../assets/images/header/map.png'


import './Header.scss'

class Header extends PureComponent {
    handleClick = () => {
        const { openPopup } = this.props;
        openPopup();
    }
    toggleClick = () =>{
        const { openToggle } = this.props;
        openToggle();  
    }

    render() {
        return (
            <div className='header'>
                <div className='header__flex header__flex-space'>
                    <div className='header__flex'>
                        <img className='header__logo' src={logo} alt="logo" />
                        <div className='header__text'>
                            <span className='header__bold'>Профессиональный монтаж дымоходов</span>
                            <span className='header__light'> любой сложности на территории Киева и Киевской обл.</span>
                        </div>
                    </div>
                    <div className='header__flex-map' onClick={this.handleClick}>
                        <img className='header__map' src={map} alt="map" />
                        <div className='header__texti' >
                            <span className='header__bold'>Украина, Киев</span>
                            <br />
                            <span className='header__light'> Васильковская, 30</span>
                        </div>
                    </div>
                    <button className='header__button transition--slide'>Консультация инженера</button>
                    <div>
                        <img src={call} alt="call" />
                        <a href="tel:+380672425878" className='header__phone'>+38 067 242-58-78</a>
                        <br />
                        <a href='#' ><img className='header__icon' src={viber} alt="viber" /></a>
                        <a href='#' ><img className='header__icon' src={telegram} alt="telegram" /></a>
                        <a href='#' ><img className='header__icon' src={whatsapp} alt="whatsapp" /></a>
                    </div>
                    <div>
                        <button class="Toggle" onClick={this.toggleClick}>
                            <span class="Toggle__display">
                                <svg xmlns="http://www.w3.org/2000/svg" width="32" height="32" class="bi" fill="currentColor" viewBox="0 0 16 16">
                                    <path fill-rule="evenodd" d="M2.5 11.5A.5.5 0 0 1 3 11h10a.5.5 0 0 1 0 1H3a.5.5 0 0 1-.5-.5zm0-4A.5.5 0 0 1 3 7h10a.5.5 0 0 1 0 1H3a.5.5 0 0 1-.5-.5zm0-4A.5.5 0 0 1 3 3h10a.5.5 0 0 1 0 1H3a.5.5 0 0 1-.5-.5z"></path>
                                </svg>
                            </span>
                        </button>
                    </div>
                </div>
            </div>
        )
    }
}
const mapDispatchToProps = dispatch => ({
    openPopup: () => dispatch(openPopup()),
    openToggle: () => dispatch(openToggle())
});

export default connect(null, mapDispatchToProps)(Header);
