import { PureComponent } from 'react';
import { openPopupSlider } from '../../actionCreators';
import { connect } from 'react-redux';
import './Calculater.scss'
import calcbtn from '../../assets/images/calculater/calcbtn.png'
import licensia1 from '../../assets/images/slider/licensia1.png'
import licensia2 from '../../assets/images/slider/licensia2.png'
import licensia3 from '../../assets/images/slider/licensia3.png'
import arrowPrew from '../../assets/images/slider/arrowPrew.png'
import arrowNext from '../../assets/images/slider/arrowNext.png'

class Calculater extends PureComponent {
  handleClick = () => {
    const { openPopupSlider } = this.props;
    openPopupSlider();
  }
  render() {
    return (
      <div className='calculater' name="Calculater">
        <div>
          <h5> Рассчитайте стоимость монтажа вашего дымохода за 1 минуту и <h6> получите скидку 15%</h6></h5>
          <button className='transition--slide calculater__imgi'></button>
        </div>
        <div>
          <h6>Лицензии и сертификаты в наличии</h6>
          <div className='calculater__flex' onClick={this.handleClick}>
            <img className='calculater__arrow' src={arrowPrew} />
            <img src={licensia1} />
            <img src={licensia2} />
            <img src={licensia3} />
            <img className='calculater__arrow' src={arrowNext} />
          </div>
        </div>
      </div>
    );
  }
}
const mapDispatchToProps = dispatch => ({
  openPopupSlider: () => dispatch(openPopupSlider())
});

export default connect(null, mapDispatchToProps)(Calculater);