const initState = {
    isToggleOpen: false,
  }
  
  function reducer(state = initState, action) {
    switch (action.type) {
      case "OPEN_TOGGLE": {
        return { ...state, isToggleOpen: true }
      }
      case "CLOSE_TOGGLE": {
        return { ...state, isToggleOpen: false }
      }
      default: return state;
    }
  }
  
  export default reducer;
  
  