import { PureComponent } from 'react';
import './PopupSlider.scss';
import { closePopupSlider } from '../../actionCreators';
import { connect } from 'react-redux';
import SimpleSlider from '../SimpleSlider';


class PopupSlider extends PureComponent {

  handleClick = () => {
    const { closePopupSlider } = this.props;
    closePopupSlider();
  }

  render() {
    const { isPopupOpenSlider } = this.props;
    if (!isPopupOpenSlider) {
      return null;
    }

    return (
      <div>
        <div className='close' onClick={this.handleClick} />
        <div className="popup">
          <div className="popup__contentSlider">
            <SimpleSlider />
          </div>
        </div>
      </div>
    );
  }
}

const mapStateToProps = ({ popupSlider }) => ({
  isPopupOpenSlider: popupSlider.isPopupOpenSlider,
});

const mapDispatchToProps = dispatch => ({
  closePopupSlider: () => dispatch(closePopupSlider())
});

export default connect(mapStateToProps, mapDispatchToProps)(PopupSlider);